'use strict';

define('common/i18n/Language',['require','../i18n/en-gb'],function(require) {
	var enGB = require('../i18n/en-gb');

	var Language = function() {
		this.language = "en-gb";

		this.setLanguage = function(language) {
			this.language = language;
		};
		
		this.getText = function() {
			return enGB;
		};
	};

	return Language;
});
