'use strict';

define('teacher/directives/Home',['require','angular','text!./home.tpl.html'],function(require) {
	var angular = require('angular');
	var template = require('text!./home.tpl.html');

	var Home = function(gameService) {
		return {
			template : template,
			restrict : 'E',
			scope : {
				teacher : '=',
				user: '='
			},
			link : function(scope, element, attrs, controller) {
			},
			controller : function($scope) {
				$scope.selectedGames = gameService.getGames();
			}
		};
	};

	return ['gameService', Home];
});

